@use 'base/variables' as v;
@use 'base/mixins' as m;

.proceso-cafe {
    margin-bottom: v.$separacion;
    @include m.tablet { 
        display: flex;
        gap: v.$separacion;
    }
    &:last-of-type {
        margin-bottom: 0;
    }
    &:nth-child(even) {
        .texto {
            order: -1;
        }
    }
    .imagen,
    .texto {
        flex: 1;
        margin-top: 1rem;
    }
    .texto {
        h3 {
            font-size: 2rem;
            margin: 0;
            text-align: left;
            color: v.$primario;

            &::after {
                content: '';
                display: block;
                width: 50%;
                height: 5px;
                background-color: v.$secundario ;
            }
        }
    }
}

.lista-servicio{
    list-style:circle;
    margin: 1rem;
    padding: 1rem;
    li{
        margin: 0 0 1rem 0;
    }
    
}

.texto-servicio{
    line-height: normal;
}